import React from 'react';
import './Note.scss';
import logo from '../../assets/jamesLogo.png';
import logoSleeping from '../../assets/jamesLogoSleep.png';

function Note({note, squished = false, active=true}){
    return (
        <div className={`note ${squished && "squished"}`}>
            <img src={active ? logo : logoSleeping} alt="" />
            <div className="speech-bubble">
                <p>note: {note}</p>
            </div>
        </div>

    )
}

export default Note;
 