import React, { useState } from 'react';
import './RecipeEntry.scss';
function App() {
  const [recipe, setRecipe] = useState({
    title: '',
    ingredients: [],
    instructions: [],
    tags: [],
    prepTime: 0,
    cookTime: 0,
    blurb: ''
  });

  const handleInputChange = (e) => {
    setRecipe({ ...recipe, [e.target.name]: e.target.value });
  };

  const handleIngredientChange = (index, e) => {
    const newIngredients = recipe.ingredients.map((ingredient, i) => {
      if (index === i) {
        return { ...ingredient, [e.target.name]: e.target.value };
      }
      return ingredient;
    });
    setRecipe({ ...recipe, ingredients: newIngredients });
  };

  const handleInstructionChange = (index, e) => {
    const newInstructions = recipe.instructions.map((instruction, i) => {
      if (index === i) {
        return { ...instruction, [e.target.name]: e.target.value };
      }
      return instruction;
    });
    setRecipe({ ...recipe, instructions: newInstructions });
  };

  const handleAddIngredient = () => {
    setRecipe({
      ...recipe,
      ingredients: [...recipe.ingredients, { amount: '', unit: '', ingredient: '', note: '' }]
    });
  };

  const handleAddInstruction = () => {
    setRecipe({
      ...recipe,
      instructions: [...recipe.instructions, { step: recipe.instructions.length + 1, instruction: '', tip: '' }]
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(JSON.stringify(recipe, null, 2));
    // Optionally, save JSON to a file or server here
    //copy to clipboard
    navigator.clipboard.writeText(JSON.stringify(recipe, null, 2));
  };

  return (
    <div className="App">
      <form onSubmit={handleSubmit}>
        <label>
          Title:
          <input type="text" name="title" value={recipe.title} onChange={handleInputChange} />
        </label>
        <label className="blurb">
          Blurb:
          <textarea name="blurb" value={recipe.blurb} onChange={handleInputChange} placeholder="Add a personal note or context about the recipe here" />
        </label>
        {recipe.ingredients.map((ingredient, index) => (
          <div key={index}>
            <input type="text" name="amount" placeholder="Amount" value={ingredient.amount} onChange={e => handleIngredientChange(index, e)} />
            <input type="text" name="unit" placeholder="Unit" value={ingredient.unit} onChange={e => handleIngredientChange(index, e)} />
            <input type="text" name="ingredient" placeholder="Ingredient" value={ingredient.ingredient} onChange={e => handleIngredientChange(index, e)} />
            <input type="text" name="note" placeholder="Note" value={ingredient.note} onChange={e => handleIngredientChange(index, e)} />
          </div>
        ))}
        <button type="button" onClick={handleAddIngredient}>Add Ingredient</button>
        {recipe.instructions.map((instruction, index) => (
          <div key={index}>
            <textarea name="instruction" placeholder="Instruction" value={instruction.instruction} onChange={e => handleInstructionChange(index, e)} />
            <textarea name="tip" placeholder="Tip" value={instruction.tip} onChange={e => handleInstructionChange(index, e)} />
          </div>
        ))}
        <button type="button" onClick={handleAddInstruction}>Add Instruction</button>

        <button type="submit">Submit Recipe</button>
      </form>
    </div>
  );
}

export default App;
