import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./Home.scss";
import logo from "../../assets/jamesLogo.png";
import RecipePage from "../RecipePage/RecipePage";
import recipes from "../../data/recipes";
import mainLogo from "../../assets/logo.svg";
import wordMark from "../../assets/wordmark.svg";
import Tip from "../../components/Tip/Tip";
import { Icon } from '@iconify/react';
import Popup from "../../components/Popup/Popup";

function Home() {
    const [opened, setOpened] = useState(false);
    const [viewport, setViewport] = useState("100vh");
    const [letterOpened, setLetterOpened] = useState(false);
    const [displayedText, setDisplayedText] = useState("");
    const [textIndex, setTextIndex] = useState(0);
    const letterText = `hello love! can you believe it's been 4 years (kinda) ?? when I say it out loud, it seems like such a long time, but in reality, time has been flying by. it seems like just yesterday when we were 16, planning our in-person meet-ups during quarantine, when we would walk around our neighborhood blushing while holding hands. Fast forward to now, where we're juniors in college planning out our adult lives, both flourishing in our adult selves, making money moves, I'd say we're unrecognizable from our crusty 16-year-old selves. In fact, growing up with you by my side has been so rewarding, you've endowed me with so much confidence in my ability, given me a real sense of purpose when I pursue the things that I pursue, I wouldn't trade it for the world. I remember early on in the relationship, when we had a discussion about when our honeymoon phase was going to end, and I'm happy to report, 4 years later, that we have yet to reach that point in our relationship. Sure, our love has grown and matured, but the butterflies in my stomach have yet to leave. Your smile still has the same effect on me as it did on my rosy-cheeked 16 year old self. You complete me in a way that I never thought possible, and I'm so grateful to have you in my life. I love you so much, and I can't wait to see what the future holds for us. <3`;
    const words = letterText.split(' ');
    const [letterFinished, setLetterFinished] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        const handleResize = () => {
            setViewport((window.innerHeight) + "px");
        };

        window.addEventListener('resize', handleResize);
        setViewport((window.innerHeight) + "px");
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    useEffect(() => {
        let interval;
        if (letterOpened) {
            interval = setInterval(() => {
                if (textIndex < words.length) {
                    setDisplayedText((prev) => prev + " " + words[textIndex]);
                    setTextIndex((prev) => prev + 1);
                } else {
                    clearInterval(interval);
                    setLetterFinished(true);
                }
            }, 10); // Adjust the speed as needed
        }
        return () => clearInterval(interval);
    }, [letterOpened, textIndex, words]);

    const closeLetter = () => {
        setLetterOpened(false);
        setDisplayedText("");
        setTextIndex(0);
    }

    const openLetter = () => {
        setLetterOpened(true);
    }

    return (
        <div className="home" style={{ height: viewport }} onClick={() => setOpened(true)}>
            <div className={`book ${opened && "open"}`}>
                <div className="title">
                    <img src={mainLogo} alt="" />
                </div>
                <p>from yours truly</p>
            </div>
            {opened && (
                <div className="index">
                    <header className="header">
                        <img src={wordMark} alt="" />
                    </header>
                    <div className="content">
                        <div className={`tip`}>
                            <img src={logo} alt="" />
                            <div className="content">
                                <Icon icon="twemoji:love-letter" width="36" height="36" onClick={openLetter} className={`letter ${letterOpened && "open"}`}/>
                                {letterOpened && (
                                    <div className="speech-bubble">
                                        <p className="tip-text">{displayedText}</p>
                                    </div>
                                )}
                                {letterFinished && <button onClick={()=>navigate('/index')}>continue</button>}
                                
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Home;
