import React from 'react';
import './Tip.scss';
import logo from '../../assets/jamesLogo.png';
import logoSleeping from '../../assets/jamesLogoSleep.png';

function Tip({tip, active=true, label=true}){
    return (
        <div className={`tip`}>
            <img src={active ? logo : logoSleeping} alt="" />
            <div className="content">
                {label && <h3>james tip...</h3>}
                <div className="speech-bubble">
                    <p className="tip-text">{tip}</p>
                </div>
            </div>
        </div>

    )
}

export default Tip;